body, p {
  margin: 0;
}

img {
  height: 250px;
  width: 370px;
}

.gallery {
  max-width: 1140px;
  grid-gap: 15px;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  margin-left: auto;
  margin-right: auto;
  padding: 50px 0;
  display: grid;
}

.photo-card {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.info {
  width: 100%;
  justify-content: space-around;
  display: flex;
}

.info-data {
  flex-direction: column;
  align-items: center;
  gap: 5px;
  margin: 10px 0;
  display: flex;
}

.form-wrapper {
  height: 50px;
  background-color: #4a61bd;
  justify-content: center;
  align-items: center;
  display: flex;
}

.load-more-btn {
  width: 150px;
  height: 50px;
  color: #fff;
  background-color: #4a61bd;
  border: none;
  border-radius: 5px;
  margin: 10px auto;
  font-size: 20px;
}

input {
  height: 25px;
  width: 200px;
  border: none;
  border-radius: 5px;
  outline: 0;
  padding-left: 10px;
}

